body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}
header {
  background-color: #333;
  color: #888;
  padding: 10px 20px;
}
.container {
  max-width: 800px;
  margin: 20px auto;
  padding: 0 20px;
}
h2 {
  margin-top: 0;
}
.login-section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}
.dashboard-section {
  display: flex;
  text-align: center;
}
.hidden {
  display: none;
}

/* Style for all buttons */
button {
  background-color: red;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
}
input[type="text"], input[type="password"] {
  background-color: lightblue;
  color: green;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
  margin-bottom: 10px; /* Add some spacing between elements */
}
button:hover {
  background-color: darkred;
}
